section.WelcomeBackground {
    display: flex;
    width: 100%;
    height: 100vh;
    background: url("../images/ariel-view-park.jpeg");
    background-size: cover;
    background-repeat: no-repeat;
}

header.WelcomeHeader {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
}

button.welcomebutton {
    height: 100px;
    width: 250px;
    font-size: 76px;
    font-family: monospace;
    cursor: pointer;
    background-color: Transparent;
    border: none;
    outline: none;
}

div.MenuBar {
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: fixed;
    width: 100%;
    height: 15vh;
}

button.MenuButton {
    height: 65px;
    width: 150px;
    font-size: 32px;
    font-family: monospace;
    cursor: pointer;
    opacity: 1;
}

button.MenuButton:hover {
    opacity: .75
}

div.HomeBackground {
    display: flex;
    width: 100%;
    height: 100vh;
    background: url("../images/home-banner-bench.jpeg");
    background-size: cover;
    background-repeat: no-repeat;
}

section.quotebox {
    width: 85%;
    height: 100vh;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    flex-wrap: wrap;
}

div.parkquote {
    display: flex;
    background-color: rgba(211, 211, 211, 0.25);
    height: 43vh;
    width: 40vw;
    font-family: monospace;
    flex-direction: column;
    flex-wrap: wrap;
    align-content: center;
    align-items: flex-end;
}

p.quote {
    font-size: 24px;
}

p.author {
    font-size: 18px;
}

section.ParksBackground {
    display: flex;
    width: 100%;
    height: 100vh;
    background: url("../images/parks-swing.jpeg");
    background-size: cover;
    background-repeat: no-repeat;
}

section.parkbox {
    top: 15vh;
    left: 35vw;
    position: absolute;
}

h1.ParksText {
    display: flex;
    flex-wrap: wrap;
    top: 15vh;
    position: absolute;
    background-color: rgba(211, 211, 211, 0.25);
    font-family: monospace;
    font-size: 36px;
    width: 23vw;
    height: 18vh;
    left: 3vw;
    position: absolute;
}

input.parkinput {
    font-family: monospace;
}

section.ParksData {
    height: 80vh;
    width: 55vw;
    overflow: auto;
    font-family: monospace;
    background-color: rgba(211, 211, 211, 0.25);
}

section.ContactBackground {
    display: flex;
    width: 100%;
    height: 100vh;
    background: url("../images/contact-tree-swing.jpeg");
    background-size: cover;
    background-repeat: no-repeat;
}

section.MapBackground {
    display: flex;
    width: 100%;
    height: 100vh;
    background: url("../images/map-bench.jpeg");
    background-size: cover;
    background-repeat: no-repeat;
}

section.mapbox {
    top: 25vh;
    left: 13vw;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 75vw;
}

ul.dispalymapaddress {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    background-color: rgba(211, 211, 211, 0.25);
    font-family: monospace;
    font-size: 20px;
    width: 13vw;
    height: 15vh;
}

section.contactbox {
    display: flex;
    width: 100%;
    height: 100vh;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

h1.ContactText {
    display: flex;
    background-color: rgba(211, 211, 211, 0.25);
    font-family: monospace;
    font-size: 36px;
    height: 8vh;
    width: 22vw;
    justify-content: center;
    align-items: center;
}

img.me {
    display: block;
    margin: 2em auto;
    margin-bottom: 3em;
    width: 200px;
    height: 200px;
    border-radius: 50%;
    position: relative;
    z-index: 2;
}

.fa {
    padding: 20px;
    font-size: 45px;
    width: 22.5px;
    text-align: center;
    text-decoration: none;
    border-radius: 50%;
}

.fa:hover {
    opacity: 0.5;
}

.fa-twitter {
    background: #55ACEE;
    color: white;
}

.fa-github {
    background: white;
    color: rgb(36, 41, 46);
}

.fa-linkedin {
    background: rgb(0, 119, 181);
    color: white;
}

.fa-code {
    background: white;
    color: rgb(31, 64, 86);
}

.fa-facebook {
    background: #3B5998;
    color: white;
}

.fa-google {
    background: rgb(234, 67, 53);
    color: white;
}